.producttitle{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 60px;
    margin-top: 0;
    margin-bottom: 16px;
}
.productdesciption{
    font-size: 20px;
    color: rgb(rgb(215, 106, 106), rgb(60, 202, 60), rgb(123, 123, 210));
    margin-top: 0;
    margin-bottom: 16px;
}
.sectionheading{
    font-size: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.productimage{
    width: 100%;
    max-width: 100px;
    border: 2px solid transparent;
    border-radius: 18px;
    box-shadow: 0 2px 4px transparent;
    margin-right: 10px;
    cursor: pointer;
  
}
.selectedimg{
    border: 2px solid teal;
    box-shadow: 0 2px 4px teal; 
}
  
.featurebtn{
    border: none;
    outline: none;
    padding: 12px 24px;
    text-align: center;
    cursor: pointer;
    background-color: skyblue;
    font-size: 25px;
    border-radius: 10px;
    margin-right: 20px;
}
.featurebtn1{
    border: none;
    outline: none;
    padding: 12px 24px;
    text-align: center;
    cursor: pointer;
    background-color: deepskyblue;
    font-size: 25px;
    border-radius: 10px;
    margin-right: 20px;
    margin-top: 20px;
}
.selectedfeature{
    background-color: deepskyblue;
    color: aliceblue;
}