
.productpreview {
    width: 50%;
    position: relative;
}
.productpreview img{
    width: 100%;
    
}
.time{
  position: absolute;
  top: 32%;
  left:40%;
  color: aliceblue;
  font-size: 25px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.heart{
